export const fetchPhotoByQuery = (searchedQuery, currentPage) => {
  const axiosOptions = {
    params: {
      key: `48208866-6baf83551ffafce9b15eedbf6`,
      q: searchedQuery,
      image_type: 'photo',
      orientation: 'horizontal',
      safesearch: 'true',
      page: currentPage,
      per_page: 15,
    },
  };

  return axios.get(`https://pixabay.com/api/`, axiosOptions);
};
